import { createApp, ref, getCurrentInstance } from 'vue'
import Toast from '../components/Toast.vue'

/**
 * @typedef {Object} ToastOptions
 * @property {string} message - Toast 
 * @property {string} type - Toast 'success' | 'warning' | 'error' | 'info'
 * @property {number} duration - 
 * @property {string} position - 'top' | 'bottom' | 'center'
 */

// * 
const defaultOptions = {
  duration: 3000,
  position: 'top'
}

// * 
let toastInstance = null  // ?  Toast 
let toastContainer = null // ?  Toast  DOM
let toastApp = null      // ?  Vue 

// *  Toast 
const createToastContainer = () => {
  if (!toastContainer) {
    toastContainer = document.createElement('div')
    toastContainer.id = 'toast-container'
    document.body.appendChild(toastContainer)
  }
  return toastContainer
}

// *  Toast 
const showToast = (options) => {
  const mergedOptions = { ...defaultOptions, ...options }
  
  if (!toastInstance) {
    const container = createToastContainer()
    toastApp = createApp(Toast, mergedOptions)
    toastInstance = toastApp.mount(container)
  } else {
    toastApp.unmount()
    const container = createToastContainer()
    toastApp = createApp(Toast, mergedOptions)
    toastInstance = toastApp.mount(container)
  }

  return new Promise((resolve) => {
    setTimeout(() => {
      resolve()
    }, mergedOptions.duration)
  })
}

// * 
export const useToast = () => {
  return {
    success: (message, duration = defaultOptions.duration) => 
      showToast({ message, type: 'success', duration }),
    warning: (message, duration = defaultOptions.duration) => 
      showToast({ message, type: 'warning', duration }),
    error: (message, duration = defaultOptions.duration) => 
      showToast({ message, type: 'error', duration }),
    info: (message, duration = defaultOptions.duration) => 
      showToast({ message, type: 'info', duration })
  }
}

// * Vue 
const ToastPlugin = {
  install: (app) => {
    app.config.globalProperties.$toast = {
      success: (message, duration = defaultOptions.duration) => 
        showToast({ message, type: 'success', duration }),
      warning: (message, duration = defaultOptions.duration) => 
        showToast({ message, type: 'warning', duration }),
      error: (message, duration = defaultOptions.duration) => 
        showToast({ message, type: 'error', duration }),
      info: (message, duration = defaultOptions.duration) => 
        showToast({ message, type: 'info', duration })
    }
  }
}

export default ToastPlugin 